import styles from "./Table.module.scss"
import Member from "@/assets/img/member.svg?react"
import Deposit from "@/assets/img/deposit.svg?react"
import Win from "@/assets/img/win.svg?react"
import Nft from "@/assets/img/nft.svg?react"
import Tokens from "@/assets/img/tokens.svg?react"
import Button from "@/components/common/Button"
import ArrowRight from "@/assets/img/arrow-right.svg?react"
import useBreakpoint from "use-breakpoint"
import { slicingHash } from "@/utils"
import { BREAKPOINTS } from "@/app/static"

function TableItem(item) {
  const { breakpoint } = useBreakpoint(BREAKPOINTS)

  function isMaxMd() {
    return breakpoint === "sm" || breakpoint === "xs" || breakpoint === "md"
  }

  function getInfo(name, isColor): string | JSX.Element {
    switch (name) {
      case "member":
        return isColor ? "green" : <Member />
      case "deposit":
        return isColor ? "magenta" : <Deposit />
      case "win":
        return isColor ? "yellow" : <Win />
      case "nft":
        return isColor ? "pink" : <Nft />
      case "tokens":
        return isColor ? "blue" : <Tokens />
    }
    return ""
  }
  // 1.5fr 2fr 1.5fr
  function isHash() {
    return item.find((el) => el.isHash)
  }

  return (
    <div
      className={styles.table__item}
      style={{
        gridTemplateColumns:
          isHash() && isMaxMd()
            ? "1.8fr 3fr 2.8fr"
            : "1fr ".repeat(item.length),
      }}
    >
      {item.map((child, idx) => {
        if (child.event) {
          return (
            <div
              key={idx}
              className={`${styles.table__value} ${
                styles[getInfo(child.event, true)]
              }`}
            >
              {getInfo(child.event, false)}
              <span>{child.text}</span>
            </div>
          )
        }
        if (child.isHash && isMaxMd()) {
          return (
            <div key={idx} className={`${styles.table__value}`}>
              <span>{slicingHash(child.text, 6)}</span>
            </div>
          )
        }
        return (
          <div key={idx} className={`${styles.table__value}`}>
            <span>{child.text}</span>
          </div>
        )
      })}
      <div className={styles.table__buttonwrap}>
        <Button className={styles.table__button} variant="text" text="More" />
        <ArrowRight />
      </div>
    </div>
  )
}

function Table({ titles, data }: { titles: string[]; data: any[] }) {
  const { breakpoint } = useBreakpoint(BREAKPOINTS)

  function isHash() {
    if (!data.length) return

    return data.find((el) => el.find((child) => child.isHash))
  }

  function isMaxMd() {
    return breakpoint === "sm" || breakpoint === "xs" || breakpoint === "md"
  }

  return (
    <div className={styles.table}>
      <div
        className={styles.table__header}
        style={{
          gridTemplateColumns:
            isHash() && isMaxMd()
              ? "1.8fr 3fr 2.8fr"
              : "1fr ".repeat(titles.length),
        }}
      >
        {titles.map((item) => {
          return (
            <div key={item} className={styles["table__header-item"]}>
              {item}
            </div>
          )
        })}
      </div>
      <div className={styles.table__content}>
        {data.map((item, idx) => {
          return (
            <div
              key={idx}
              style={{
                background: idx % 2 !== 0 ? "rgba(255, 255, 255, 0.01)" : "",
              }}
            >
              {TableItem(item)}
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default Table
