import { useState } from "react"
import {
  PopupError,
  PopupNotification,
  PopupSuccess,
  PopupBalance,
} from "@/components/elements/Popups"
import Button from "@/components/common/Button"

function Popups() {
  const [isError, setError] = useState(false)
  const [isNotification, setNotification] = useState(false)
  const [isSuccess, setSuccess] = useState(false)
  const [isBalance, setBalance] = useState(false)

  return (
    <div>
      <Button text="SHOW ERROR" onClick={() => setError(true)} />
      <PopupError isActive={isError} close={() => setError(false)} />
      <Button text="SHOW NOTIFICATION" onClick={() => setNotification(true)} />
      <PopupNotification
        isActive={isNotification}
        close={() => setNotification(false)}
      />

      <Button text="SHOW SUCCESS" onClick={() => setSuccess(true)} />
      <PopupSuccess isActive={isSuccess} close={() => setSuccess(false)} />

      <Button text="SHOW BALANCE" onClick={() => setBalance(true)} />
      <PopupBalance isActive={isBalance} close={() => setBalance(false)} />
    </div>
  )
}

export default Popups
