import { Helmet } from 'react-helmet';
import { useTranslation } from 'react-i18next';
import { twitterUsername } from '@/config';



export default function AppSeo({ title, description, meta, children }) {
  const { i18n, t } = useTranslation();
  //const defaultTitle = `${t('ProjectName')} - ${t('ProjectSlogan')}`;
  const defaultTitle = `${t('ProjectName')}`;
  //const defaultTitle = `World Millionaire Challenge`;
  const metaDescription = description || defaultTitle;

  return (
    <>
      <Helmet
        htmlAttributes={{
          lang: i18n.language,
        }}

        title={title}
        defaultTitle={defaultTitle}
        titleTemplate={defaultTitle ? `%s | ${defaultTitle}` : undefined}

        meta={[
          {
            name: `description`,
            content: metaDescription,
          },
          {
            property: `og:title`,
            content: title,
          },
          {
            property: `og:description`,
            content: metaDescription,
          },
          {
            property: `og:type`,
            content: `website`,
          },
          {
            name: `twitter:card`,
            content: `summary_large_image`,
          },
          {
            name: `twitter:creator`,
            content: twitterUsername ? twitterUsername : '',
          },
          {
            name: `twitter:title`,
            content: title,
          },
          {
            name: `twitter:description`,
            content: metaDescription,
          },
        ].concat(meta)}
      />
      {children}
    </>
  );
}
