import { useEffect, useState } from 'react';
import { ToastContainer } from 'react-toastify';
import { BrowserRouter } from 'react-router-dom';
import { I18nextProvider } from 'react-i18next';
import i18n from './i18n';

import { Provider as StoreProvider } from 'react-redux';
import { store } from '@/app/store';



import { EthereumClient, w3mConnectors, w3mProvider, } from '@web3modal/ethereum';
//import { infuraProvider, } from '@wagmi/core/providers/infura';
import { jsonRpcProvider } from '@wagmi/core/providers/jsonRpc';
import { Web3Modal } from '@web3modal/react';
import { configureChains, createConfig, WagmiConfig } from 'wagmi';
import { bsc, goerli } from 'wagmi/chains';
import { walletconnectProjectId, jsonRpcEndpointUrl, defaultChainId, } from '@/config';



// 2. Configure wagmi client
const chains = [];
//if (defaultChainId === 1) chains.push(mainnet);
if (defaultChainId === 56) chains.push(bsc);
if (defaultChainId === 5) chains.push(goerli);

const { publicClient } = configureChains(
  chains,
  [
    //jsonRpcProvider(jsonRpcEndpointUrl),
    w3mProvider({ projectId: walletconnectProjectId, }),
  ]
);
const wagmiConfig = createConfig({
  autoConnect: true,
  connectors: w3mConnectors({ chains, projectId: walletconnectProjectId, }),
  publicClient,
});


// 3. Configure modal ethereum client
const ethereumClient = new EthereumClient(wagmiConfig, chains);



export default function Providers({ children, }: any) {
  const [ready, setReady] = useState(false);
  useEffect(() => {
    setReady(true);
  }, []);

  if (!ready) return (null);

  return (
    <>
      <WagmiConfig config={wagmiConfig}>
        <I18nextProvider i18n={i18n}>
          <StoreProvider store={store}>
            <BrowserRouter>
              {children}
            </BrowserRouter>
          </StoreProvider>
        </I18nextProvider>
      </WagmiConfig>

      <ToastContainer
        position='top-right'
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
      //autoClose={2500}
      //rtl={false}
      //pauseOnFocusLoss
      //pauseOnHover
      //style={{ zIndex: 10000, }}
      //draggable
      />

      <Web3Modal
        themeMode='dark'
        projectId={walletconnectProjectId}
        ethereumClient={ethereumClient}
        enableExplorer={true}
        //explorerExcludedWalletIds={'ALL'}
        explorerRecommendedWalletIds={[
          'c57ca95b47569778a828d19178114f4db188b89b763c899ba0be274e97267d96',
          '4622a2b2d6af1c9844944291e5e7351a6aa24cd7b23099efac1b2fd875da31a0',
          //'19177a98252e07ddfc9af2083ba8e07ef627cb6103467ffebb3f8f4205fd7927',
        ]}
      />
    </>
  );
}
