import { useState } from 'react';
import { useTranslation } from 'react-i18next';

const regExpEmail = new RegExp(/^[\w-\.]+@([\w-]+\.)+[\w-]{2,}$/, 'g');



function SubForm() {
  const { t } = useTranslation('FooterSubscribeForm');

  const [email, setEmail] = useState('');
  const onChangeEmail = (e: any) => {
    setEmail(e.target.value.trim().toLowerCase());
  }

  const [isProcessing, setProcessing] = useState(false);
  const onSubmit = (e: any) => {
    e.preventDefault();
    if (isProcessing) return;
    if (!regExpEmail.test(email)) {
      // message incorrect
      return;
    }

    setProcessing(true);
    try {
      /*const res = await api.sendSubscribeRequest(email);
      if (res.ok) {
        openSubscribeSuccessModal();
      } else {
        openSubscribeErrorModal();
      }*/
    } catch (error) {
      //openSubscribeErrorModal();
      console.error(error);
    }

    setEmail('');
    setProcessing(false);
  }


  return (
    <form className={styles.subscribe}>
      <div className={styles.subscribe__top}>
        <h3 className={styles.subscribe__title}>{t('Name')}</h3>
        <div className={styles.subscribe__social}>
          <LinkExternal href={twitterLink}>
            <Twitter />
          </LinkExternal>
          <LinkExternal href={telegramLink}>
            <Telegram />
          </LinkExternal>
          <LinkExternal href={discordLink}>
            <Discord />
          </LinkExternal>
        </div>
      </div>
      <p className={styles.subscribe__text}>
        {t('Description')}
      </p>
      <div className={styles.subscribe__bottom}>
        <Input
          required={true}
          id='email' name='email' type='text'
          pattern={regExpEmail}
          placeholder={t('InputPlaceholder')}
          value={email}
          onChange={onChangeEmail}
        />
        <Button
          className={styles.subscribe__button}
          text={t('ButtonSubmit')}
          onClick={onSubmit}
        />
      </div>
    </form>
  );
}

