import styles from "./SlidingPopup.module.scss"
import WalletIcon from "@/assets/img/wallet.svg?react"
import WarningIcon from "@/assets/img/warning.svg?react"
import SuccessIcon from "@/assets/img/success.svg?react"
import ErrorIcon from "@/assets/img/error.svg?react"
import CloseIcon from "@/assets/img/close.svg?react"
import { useEffect, useState } from "react"
type variant = "balance" | "notification" | "success" | "error"

function CurrentIcon({ variant }: { variant: variant }) {
  switch (variant) {
    case "balance":
      return <WalletIcon />
    case "notification":
      return <WarningIcon />
    case "success":
      return <SuccessIcon />
    case "error":
      return <ErrorIcon />
  }
}

function Popup({
  variant,
  title,
  desc,
  isActive,
  close,
}: {
  variant: variant
  title: string
  desc?: string
  isActive?: boolean
  close: () => void
}) {
  return (
    <div
      className={`${styles.popup} ${isActive ? styles["popup--active"] : ""} ${
        styles[variant]
      }`}
    >
      <div className="container-xl">
        <div
          className={`${styles.popup__content} ${
            !desc?.length ? styles.popup__single : ""
          }`}
        >
          <div className={styles.popup__main}>
            <p className={styles.popup__title}>
              <CurrentIcon variant={variant} />
              {title}
            </p>
            <p className={styles.popup__text}>{desc}</p>
          </div>
          <button className={styles.popup__close} onClick={close}>
            <CloseIcon />
          </button>
        </div>
      </div>
    </div>
  )
}

export default Popup
