import Popup from "@/components/modals/SlidingPopup"

function PopupBalance({
  isActive,
  close,
}: {
  isActive: boolean
  close: () => void
}) {
  return (
    <Popup
      variant="balance"
      title="The balance is replenished"
      isActive={isActive}
      close={close}
    />
  )
}

